import React from 'react';

import firebase from '../firebase/firebase';
import './myprojects.scss';





class MyProjectComponent extends React.Component {
  constructor(props){
    super(props);
    this.state={
      project:[],
    };
  };


componentDidMount(){
  const projectRef=firebase.database().ref('project');
  projectRef.on('value',(snapshot)=>{
    let project=snapshot.val();
    let newState=[];
    for (let projectUrl in project){
      newState.push({
        id:projectUrl,
        projectUrl:project[projectUrl].projectUrl,
        projectName:project[projectUrl].projectName,
        projectImage:project[projectUrl].projectImage
      });
    }
    this.setState({
      project:newState
    });
  });
}
render(){
  return(

<div className='cardlist-container'>
{
  this.state.project.map((projectUrl)=>{
    return(
      
        <div className='card-container'>
  <a href={projectUrl.projectUrl}  target='_blanck' > <img className='project-image'  src={projectUrl.projectImage} alt='photographi' /> </a>    
            <div>
            <a href={projectUrl.projectUrl}  target='_blanck' className='name'>{projectUrl.projectName}</a>
            </div>
   </div>
   
    );
  })
}
</div>

  );
}

}

export default MyProjectComponent;