import React from 'react';
import ReactDOM from 'react-dom';
import { Switch, Route, HashRouter} from 'react-router-dom';
import './index.css';
import Navbar from './navbar-component/navbar';
import App from './App';
import PortfolioEn from './portfolio-en';
import PortfolioSr from './portfolio-sr'
import video from './video.mp4';
import 'aos/dist/aos.css';
import './portfolio.css';



ReactDOM.render(
  <React.StrictMode>
 <HashRouter>
    <video autoPlay loop muted 
    style={{
      position:'fixed',width:'100%',left:'50%',top:'50%',height:'100%',objectFit:'cover',transform:'translate(-50%,-50%',zIndex:'-1'
    }}
    > 
    <source src={video} type='video/mp4'/>
    </video>
   <Navbar/>
    <App />
    <Switch>
<Route exact path ='/' component={PortfolioEn}/>
  <Route exact path='/sr' component={PortfolioSr}/>
    </Switch>
 
    </HashRouter>
  </React.StrictMode>,
  document.getElementById('root')
);


